<template>
  <div class="modal-leave">
    <h5>Do you really want to leave this page?</h5>
    <div class="modal-btns">
      <button 
        @click="leavePage"
        class="btn-flat red-text"
      >Leave</button>
      <button 
        @click="showModal(false)"
        class="btn"
      >Stay</button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: 'ModalLeave',

  computed: {
    ...mapGetters('modals', ['getLeaveToPagePath'])
  },

  methods: {
    ...mapMutations('modals', ['showModal', 'setAllowLeavePage']),

    leavePage() {
      this.setAllowLeavePage(true)
      this.showModal(false)
      this.$router.push(this.getLeaveToPagePath)
    }
  }

}
</script>
