<template>
  <li class="todo-list__item">
    <input 
      v-if="isEdit"
      v-focus
      :value="todo.title"
      @input="changeTitle"
      @blur="leaveFocus"
      @keyup="enterTitle"
      type="text"
      class="browser-default"
    >
    <div v-else
      class="todo-inline"
    >
      <label>
        <input 
          :checked="todo.checkedIn"
          @change="checkedInput"
          type="checkbox" class="filled-in"
        >
        <span
          :class="[todo.checkedIn ? 'checked-t' : '']"
        >
          {{ todo.title }}
        </span>
      </label>
      <button 
        @click="isEdit = true"
        class="btn-flat btn-pd-0"
      >
        <i class="material-icons">edit</i>
      </button>
      <button 
        @click="remove"
        class="btn-flat btn-pd-0"
      >
        <i class="material-icons">delete</i>
      </button>
    </div>
  </li>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'EditTodo',
  props: ['id', 'editField'],

  data() {
    return {
      isEdit: this.editField,
      newTitle: ''
    }
  },

  computed: {
    ...mapGetters('defaultNote', ['getTodo']),

    todo() {
      return this.getTodo(this.id)
    }
  },

  methods: {
    ...mapMutations('defaultNote', ['todoChecked', 'todoRemove', 'todoEditTitle']),

    changeTitle() {
      this.newTitle = event.target.value
    },

    enterTitle(event) {
      if (event.keyCode === 13) {
        this.isEdit = false
      }
    },

    leaveFocus() {
      if (!this.newTitle.trim()) {
        this.remove(this.id)
      } else {
        this.todoEditTitle({
          id: this.id,
          value: this.newTitle
        })
      }

      this.isEdit = false
    },

    checkedInput() {
      this.todoChecked({
        id: this.id,
        checked: event.target.checked
      })
    },

    remove() {
      this.todoRemove(this.id)
    }
  }
}
</script>

<style lang="scss">
  .todo-list__item {
    padding: 0 10px;

    &:hover {
      background-color: rgba(#ccc, 0.15);
    }

    &:last-child {
      margin-bottom: 0;
    }

    input[type="text"] {
      width: 100%;
      font-size: 1.1rem;
      height: 24px;
      padding-left: 5px;
      padding-right: 5px;
    }
  }

  .todo-inline {
    display: flex;
    align-items: flex-start;
    color: #444;

    .btn-flat {
      margin: 10px 0 10px 10px;
      line-height: 20px;
      font-size: 24px;
      height: 20px;
    }
    
    label {
      display: block;
      padding: 10px 0;
      flex-grow: 1;
    }

    [type="checkbox"]+span:not(.lever) {
      height: 20px;
      line-height: 20px;
      font-size: 1.2rem;
      color: #444;
    }
  }

  [type="checkbox"]+span:not(.lever).checked-t {
    text-decoration: line-through;
    color: #999;
  }
</style>
