<template>
  <div class="edit-todo-list">
    <ul class="todo-list browser-default">
      <edit-todo
        v-for="todo in todos"
        :key="todo.id"
        :id="todo.id"
        :editField="isEdit"
      ></edit-todo>
    </ul>

    <button 
      @click="createTodoItem"
      class="btn-flat add-todo-item btn-pd-0 blue-text"
    >
      Add new todo item
      <i class="material-icons right">add</i>
    </button>
  </div>
</template>

<script>
import EditTodo from '@/components/EditTodo.vue'
import { mapMutations } from 'vuex'

export default {
  name: 'EditTodoList',
  props: ['todos'],

  components: {
    EditTodo
  },

  data() {
    return {
      isEdit: false
    }
  },

  methods: {
    ...mapMutations('defaultNote', ['createTodo']),

    createTodoItem() {
      this.createTodo()
      this.isEdit = true
    }
  }
}
</script>

<style lang="scss">
  .todo-list {
    margin: 20px 0;
    padding: 0 0 0 15px;
    list-style-type: none;
  }

  .add-todo-item {
    &:hover {
      text-decoration: underline;
    }
  }
</style>
