<template>
  <div 
    @click="isEdit = true"
    class="edit-title"
  >
    <div class="edit-title__text">
      <input 
        v-if="isEdit"
        v-model="titleText"
        v-focus
        @blur="leaveFocus"
        @keyup="enterTitle"
        type="text"
        class="browser-default"
      >
      <template v-else>
        {{ titleText }}
      </template>
    </div>

    <button class="btn-flat btn-pd-0">
      <i class="material-icons">edit</i>
    </button>
  </div>
</template>

<script>
export default {
  name: 'EditTitle',
  props: ['title'],

  data() {
    return {
      isEdit: false,
      titleText: this.title
    }
  },

  methods: {
    enterTitle(event) {
      if (event.keyCode === 13) {
        this.isEdit = false
      }
    },

    leaveFocus() {
      this.isEdit = false

      if (!this.titleText.trim()) {
        this.titleText = this.title
      }

      this.$emit('title-changed', this.titleText)
    }
  }
}
</script>

