<template>
  <div class="modal-delete">
    <h5>Do you really want to delete this note?</h5>
    <div class="modal-btns">
      <button 
        @click="removeItem"
        class="btn-flat red-text"
      >Delete</button>
      <button 
        @click="showModal(false)"
        class="btn"
      >Stay</button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";

export default {
  name: 'ModalDelete',

   computed: {
    ...mapGetters('modals', ['getLeaveToPagePath']),
    ...mapGetters('defaultNote', ['getDefaultNote'])
  },

  methods: {
    ...mapActions(['removeNote']),
    ...mapMutations('modals', ['showModal', 'setAllowLeavePage']),

    removeItem() {
      this.setAllowLeavePage(true)
      this.showModal(false)
      this.$router.push('/')

      this.removeNote(this.getDefaultNote.id)
    }
  }

}
</script>
